import React, { Component } from 'react';
import { Navbar, FormGroup, FormControl, Button, Nav, NavItem } from 'react-bootstrap';

class Navigation extends Component {
  render() {
    return (
        <Navbar>
        <Navbar.Header>
          <Navbar.Brand>
            <a href="#home">Robot Control System</a>
          </Navbar.Brand>
          <Navbar.Toggle />
          <Nav>
            <NavItem eventKey={1} href="#">History</NavItem>
            <NavItem eventKey={2} href="#">Data Collected</NavItem>
            </Nav>
        </Navbar.Header>
        <Navbar.Collapse>
          <Navbar.Form pullRight>
            <FormGroup>
              <FormControl type="text" placeholder="Search Logs" />
            </FormGroup>{' '}
            <Button type="submit">Submit</Button>
          </Navbar.Form>
        </Navbar.Collapse>
      </Navbar>
    );
  }
}

export default Navigation;



