import React, { Component } from 'react';
import { Image, Nav, NavItem } from 'react-bootstrap';
import ImageView from './imageview';
import LiveView from './liveview';
import DecisionView from './decisionmap';

class TabbedNav extends Component {
    constructor(props) {
        super(props);
        this.state = {panelKey:1};
        this.handleSelect = this.handleSelect.bind(this);
        
      }
    handleSelect(eventKey) {
        this.setState({panelKey:eventKey});
      }
      componentDidMount(){
        this.setState({panelKey:1});
      }
  render() {
    return (
        <div>
        <Nav bsStyle="tabs" activeKey="1" onSelect={k => this.handleSelect(k)}>
            <NavItem eventKey="1" title="Item1">
            Last Position
            </NavItem>
            <NavItem eventKey="2" title="Item2">
            Live View
            </NavItem>
            <NavItem eventKey="3" title="Item3">
            Path Map
            </NavItem>
      </Nav>
      <div>
          {this.state.panelKey == 1 && <ImageView></ImageView>}
          {this.state.panelKey == 2 && <LiveView></LiveView>}
          {this.state.panelKey == 3 && <DecisionView></DecisionView>}
      </div>
      </div>
    );
  }
}

export default TabbedNav;



