import React, { Component } from 'react';
import { Image } from 'react-bootstrap';

class ImageView extends Component {
  render() {
    return (
        <div>
            <Image src='../assets/image/1.png' alt="Image View" responsive></Image>
        </div>
    );
  }
}

export default ImageView;



